import React from 'react';
import './css/truthTable.css';
import {TableSquare, SelectionButton} from './buildComponents.js';

// TDY TKAJC SE PRAVDIVOSTN TABULKY
export default class TruthTable extends React.Component {
  
    render() {
      const sel = this.props.squares;
      const typeMap = this.props.typeMap;
      const perm = this.props.perm;
      let i = 0;
  
      return (
        <div key={i++}>
          <TableH
            typeMap={typeMap}
            key={i++} />
          <div className="bodyTruthTable" key={i++}>
            <Permutation
              key={i++}
              typeMap={typeMap}
              perm={perm}
            />
            <TableValSelection
              squares={sel}
              typeMap={typeMap}
              perm={perm}
              key={i++}
              onClick={(i, j) => this.props.onClick(i, j)}
              setRowOrColCell={(i, j, k, val) => this.props.setRowOrColCell(i, j, k, val)} />
          </div>
        </div>
      );
    }
  
  }
  
class Permutation extends React.Component { // komponenta, kter generuje permutace

    renderTableSquare(val, i) { // generuje jednotliv buky s hodnotou 0 nebo 1
      return (
        <TableSquare
          value={val}
          key={i}
          className="square tableFont"
        />
      );
    }
renderTableCol2(j, perm) {
  const typeMap = this.props.typeMap;

  let html = [];

  if (typeMap === 4) {
    for (let i = 0; i < 16; i++) {
      // B C A D => dekdovno z indexu i
      const b = (i >> 3) & 1;
      const c = (i >> 2) & 1;
      const a = (i >> 1) & 1;
      const d = i & 1;

      const reordered = [b, c, a, d]; // [B, C, A, D]
      html.push(this.renderTableSquare(reordered[j], i));
    }
  } else {
    const temp = Math.pow(2, typeMap);
    for (let i = 0; i < temp; i++) {
      html.push(this.renderTableSquare(perm[i][j], i));
    }
  }

  return html;
}

  
    renderTableCol(j, perm) { 
      return (
        <div className="table-col" key={j}>
          {this.renderTableCol2(j, perm) // tato funkce skuten generuje (vykresluje) buky
          }
        </div>
      );
    }
    renderTablePermutation(perm) {
      const typeMap = this.props.typeMap;
      var html = [];      // nvratov pole, do kterho vkldm (pushuju) prvky
  
      for (let j = 0; j < typeMap; j++)   // cyklus for, kter generuje sloupce
        html.push(this.renderTableCol(j, perm)); // vloen (push) sloupc do pole, kter bude nsledn vloeno do divu s permutacemi 
  
      return html;
    }
    render() {
      const perm = this.props.perm;
      // volm funkci pro vygenerovn permutac
      return this.renderTablePermutation(perm);
    }
  }
  
class TableH extends React.Component {    // komponenta, kter generuje hlaviku tabulky
    renderTableHead(a, i) {
      return (
        <TableSquare value={a} key={i} k={i} className="square tableFont" />
      );
    }
  
renderTableRow() {
  const typeMap = this.props.typeMap;
  let alphabet;

  if (typeMap === 2) {
    alphabet = ["B", "A"];      // pro 2 promnn
  } else if (typeMap === 3) {
    alphabet = ["C", "B", "A"]; // pro 3 promnn
  } else {
    alphabet = ["D", "C", "B", "A"]; // pro 4 promnn (nebo jin)
  }

  let a = [];
  let i = 0;
  for (; i < typeMap; i++)
    a.push(this.renderTableHead(alphabet[i], i));
  a.push(this.renderTableHead("f", i + 1));
  return a;
}

    render() {
      return <div className="tableHead" key={-1}> {this.renderTableRow()} </div>;
    }
  }
    
class TableValSelection extends React.Component { // komponenta, kter generuje tlatka tabulky
  
    renderSelectionButton(i, j, k) {
      return (
        <SelectionButton
          value={this.props.squares[i][j][0]}
          key={k}
          onClick={() => this.props.onClick(i, j)}
        />
      );
    }
  
    renderTableCol() {
      const typeMap = this.props.typeMap;
      const squares = this.props.squares;
      let a = [];
      let r = typeMap;
      let c = typeMap;
      if (typeMap === 3) {
        c = 4;
        r = 2;
      }
      let key = 0;
    
      const customOrder4 = [
        [0, 2, 6, 4],
        [1, 3, 7, 5],
        [9, 11, 15, 13],
        [8, 10, 14, 12]
      ];
    
      if (typeMap === 4) {
        // === 4 promnn: sprvn poad 0-15 ===
        for (let targetDecimal = 0; targetDecimal <= 15; targetDecimal++) {
          for (let row = 0; row < 4; row++) {
            for (let col = 0; col < 4; col++) {
              const currentDecimal = customOrder4[row][col];
              if (currentDecimal === targetDecimal) {
                a.push(this.renderSelectionButton(row, col, key++));
              }
            }
          }
        }
      } else {
        // === star chovn pro 2 a 3 promnn ===
        for (let i = 0; i < c; i++) {
          let l;
          if (i === 2) l = 3;
          else if (i === 3) l = 2;
          else l = i;
          for (let j = 0; j < r; j++) {
            let k;
            if (j % r === 2) k = 3;
            else if (j % r === 3) k = 2;
            else k = j;
            a.push(this.renderSelectionButton(k, l, key++));
          }
        }
      }
    
      return a;
    }
  
    render() {
      return <div className="table-col-selButton"> {this.renderTableCol()} </div>;
    }
  } 